        @click="activeTab = 'cart'"
      >
        –ö–æ—Ä–∑–∏–Ω–∞
        <span class="badge" v-if="cartItems.length > 0">{{ cartItems.length }}</span>
      </button>
      <button 
        class="tab h4 montserrat-semibold"
        :class="{ active: activeTab === 'favorites' }"
        @click="activeTab = 'favorites'"
      >
        –ò–∑–±—Ä–∞–Ω–Ω–æ–µ
        <span class="badge" v-if="favoriteBoxes.length > 0">{{ favoriteBoxes.length }}</span>
      </button>
    </div>

    <!-- Cart Section -->
    <div v-if="activeTab === 'cart'" class="cart-section">
      <div class="cart-content">
        <div class="cart-items">
          <div v-if="cartItems.length === 0" class="empty-state">
            <div class="empty-icon">üõí</div>
            <h3 class="h4 montserrat-semibold white-text">–ö–æ—Ä–∑–∏–Ω–∞ –ø—É—Å—Ç–∞</h3>
            <p class="p-small montserrat-regular gray-text">–î–æ–±–∞–≤—å—Ç–µ –±–æ–∫—Å—ã –∏–∑ –∏–∑–±—Ä–∞–Ω–Ω–æ–≥–æ –∏–ª–∏ –∫–∞—Ç–∞–ª–æ–≥–∞</p>
          </div>

          <div v-for="item in cartItems" :key="item.id" class="cart-item">
            <div class="item-image">
              <img :src="item.box.coverImage" :alt="item.box.name" />
            </div>

            <div class="item-info">
              <h3 class="p montserrat-semibold white-text">{{ item.box.name }}</h3>
              <span v-if="item.isModified" class="modified-badge montserrat-medium">
                –°–æ—Å—Ç–∞–≤ –∏–∑–º–µ–Ω—ë–Ω
              </span>
              <p class="p-small montserrat-regular gray-text composition-label">–í —Å–æ—Å—Ç–∞–≤–µ:</p>
              <div class="composition">
                <span 
                  v-for="(comp, index) in item.displayComposition.slice(0, 4)" 
                  :key="index"
                  class="p-small montserrat-regular gray-text"
                >
                  {{ comp.name }} √ó {{ comp.quantity }}{{ index < Math.min(item.displayComposition.length, 4) - 1 ? ', ' : '' }}
                </span>
                <span v-if="item.displayComposition.length > 4" class="p-small montserrat-regular second-text">
                  +{{ item.displayComposition.length - 4 }} –µ—â—ë
                </span>
              </div>
            </div>

            <div class="item-controls">
              <button 
                class="edit-btn montserrat-medium"
                @click="goToBoxPage(item.box.slug)"
                title="–ò–∑–º–µ–Ω–∏—Ç—å —Å–æ—Å—Ç–∞–≤"
              >
                ‚úé –ò–∑–º–µ–Ω–∏—Ç—å
              </button>

              <div class="item-price">
                <div class="p montserrat-bold white-text">{{ formatPrice(item.totalPrice) }} ‚ÇΩ</div>
                <div class="p-small montserrat-regular gray-text">–∑–∞ –±–æ–∫—Å</div>
              </div>

              <button 
                class="delete-btn montserrat-regular"
                @click="removeFromCart(item.id)"
                title="–£–¥–∞–ª–∏—Ç—å"
              >
                ‚úï
              </button>
            </div>
          </div>
        </div>

        <!-- Order Summary -->
        <div class="order-summary" v-if="cartItems.length > 0">
          <h3 class="h5 montserrat-semibold white-text">–ò—Ç–æ–≥–æ</h3>
          
          <div class="promo-code">
            <input 
              type="text" 
              v-model="promoCode"
              placeholder="–í–≤–µ–¥–∏—Ç–µ –ø—Ä–æ–º–æ–∫–æ–¥"
              class="p-small montserrat-regular"
            />
            <button class="button-gray montserrat-medium" @click="applyPromo">
              –ü—Ä–∏–º–µ–Ω–∏—Ç—å
            </button>
          </div>

          <div class="summary-row">
            <span class="p montserrat-regular gray-text">–¢–æ–≤–∞—Ä—ã ({{ totalItems }} —à—Ç.)</span>
            <span class="p montserrat-semibold white-text">{{ formatPrice(subtotal) }} ‚ÇΩ</span>
          </div>

          <div class="summary-row" v-if="discount > 0">
            <span class="p montserrat-regular second-text">–°–∫–∏–¥–∫–∞</span>
            <span class="p montserrat-semibold second-text">‚àí{{ formatPrice(discount) }} ‚ÇΩ</span>
          </div>

          <div class="summary-row">
            <span class="p montserrat-regular gray-text">–î–æ—Å—Ç–∞–≤–∫–∞</span>
            <span class="p montserrat-regular gray-text">{{ deliveryCost === 0 ? '–ë–µ—Å–ø–ª–∞—Ç–Ω–æ' : formatPrice(deliveryCost) + ' ‚ÇΩ' }}</span>
          </div>

          <div class="divider"></div>

          <div class="summary-row total">
            <span class="h5 montserrat-bold white-text">–ò—Ç–æ–≥–æ</span>
            <span class="h4 montserrat-bold gradient-second-text">{{ formatPrice(total) }} ‚ÇΩ</span>
          </div>
          
          <button class="button-second montserrat-semibold checkout-btn" @click="OrderCart">
            –û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑
          </button>

          <p class="p-small montserrat-regular gray-text delivery-info">
            ‚ú® –ë–µ—Å–ø–ª–∞—Ç–Ω–∞—è –¥–æ—Å—Ç–∞–≤–∫–∞ –ø—Ä–∏ –∑–∞–∫–∞–∑–µ –æ—Ç 5000 ‚ÇΩ
          </p>
        </div>
      </div>
    </div>

    <!-- Favorites Section openCheckoutModal –ü–û–¢–û–ú –í –û–§–û–†–ú–õ–ï–ù–ò–ï -->
    <div v-if="activeTab === 'favorites'" class="favorites-section">
      <div v-if="favoriteBoxes.length === 0" class="empty-state">
        <div class="empty-icon">‚ô°</div>
        <h3 class="h4 montserrat-semibold white-text">–ò–∑–±—Ä–∞–Ω–Ω–æ–µ –ø—É—Å—Ç–æ</h3>
        <p class="p-small montserrat-regular gray-text">–î–æ–±–∞–≤–ª—è–π—Ç–µ –±–æ–∫—Å—ã –≤ –∏–∑–±—Ä–∞–Ω–Ω–æ–µ, —á—Ç–æ–±—ã –Ω–µ –ø–æ—Ç–µ—Ä—è—Ç—å</p>
      </div>

      <div class="favorites-grid">
        <div v-for="box in favoriteBoxes" :key="box.id" class="favorite-card">
          <div class="card-image">
            <img :src="box.coverImage" :alt="box.name" />
            <button 
              class="remove-favorite-btn"
              @click="removeFromFavorites(box.id)"
              title="–£–¥–∞–ª–∏—Ç—å –∏–∑ –∏–∑–±—Ä–∞–Ω–Ω–æ–≥–æ"
            >
              ‚úï
            </button>
          </div>

          <div class="card-content">
            <h3 class="p montserrat-semibold white-text">{{ box.name }}</h3>
            <p class="p-small montserrat-regular gray-text composition-label">–í —Å–æ—Å—Ç–∞–≤–µ:</p>
            <div class="composition">
              <span 
                v-for="(product, index) in getShortCompositionNames(box.id).slice(0, 3)" 
                :key="index"
                class="p-small montserrat-regular gray-text"
              >
                {{ product }}{{ index < Math.min(getShortCompositionNames(box.id).length, 3) - 1 ? ', ' : '' }}
              </span>
              <span v-if="getShortCompositionNames(box.id).length > 3" class="p-small montserrat-regular second-text">
                +{{ getShortCompositionNames(box.id).length - 3 }}
              </span>
            </div>

            <div class="card-footer">
              <div class="p montserrat-bold white-text">{{ formatPrice(calculateBoxPrice(box.id)) }} ‚ÇΩ</div>
              <button 
                class="button-second montserrat-medium add-to-cart-btn"
                @click="addBoxToCart(box.id)"
              >
                –í –∫–æ—Ä–∑–∏–Ω—É
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Checkout Modal -->
    <div v-if="showCheckoutModal" class="modal-overlay" @click="closeCheckoutModal">
      <div class="modal-content" @click.stop>
        <button class="modal-close" @click="closeCheckoutModal">‚úï</button>
        
        <h2 class="h4 montserrat-bold white-text">–û—Ñ–æ—Ä–º–ª–µ–Ω–∏–µ –∑–∞–∫–∞–∑–∞</h2>
        <p class="p-small montserrat-regular gray-text modal-subtitle">–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –¥–∞–Ω–Ω—ã–µ –¥–ª—è –¥–æ—Å—Ç–∞–≤–∫–∏</p>

        <form @submit.prevent="submitOrder" class="checkout-form">
          <div class="form-group">
            <label class="p-small montserrat-medium white-text">–ò–º—è</label>
            <input 
              type="text" 
              v-model="orderForm.name"
              placeholder="–í–∞—à–µ –∏–º—è"
              class="p montserrat-regular"
              required
            />
          </div>

          <div class="form-group">
            <label class="p-small montserrat-medium white-text">–¢–µ–ª–µ—Ñ–æ–Ω</label>
            <input 
              type="tel" 
              v-model="orderForm.phone"
              placeholder="+7 (999) 999-99-99"
              class="p montserrat-regular"
              required
            />
          </div>

          <div class="form-group">
            <label class="p-small montserrat-medium white-text">Telegram</label>
            <input 
              type="text" 
              v-model="orderForm.telegram"
              placeholder="@username"
              class="p montserrat-regular"
              required
            />
          </div>

          <div class="form-group">
            <label class="p-small montserrat-medium white-text">–ê–¥—Ä–µ—Å –¥–æ—Å—Ç–∞–≤–∫–∏</label>
            <textarea 
              v-model="orderForm.address"
              placeholder="–£–ª–∏—Ü–∞, –¥–æ–º, –∫–≤–∞—Ä—Ç–∏—Ä–∞"
              class="p montserrat-regular"
              rows="3"
              required
            ></textarea>
          </div>

          <div class="form-group">
            <label class="p-small montserrat-medium white-text">–ö–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π –∫ –∑–∞–∫–∞–∑—É</label>
            <textarea 
              v-model="orderForm.comment"
              placeholder="–ü–æ–∂–µ–ª–∞–Ω–∏—è –ø–æ –¥–æ—Å—Ç–∞–≤–∫–µ –∏–ª–∏ —É–ø–∞–∫–æ–≤–∫–µ"
              class="p montserrat-regular"
              rows="2"
            ></textarea>
          </div>

          <div class="modal-summary">
            <div class="summary-row">
              <span class="p montserrat-regular gray-text">–ò—Ç–æ–≥–æ –∫ –æ–ø–ª–∞—Ç–µ:</span>
              <span class="h5 montserrat-bold gradient-second-text">{{ formatPrice(total) }} ‚ÇΩ</span>
            </div>
          </div>

          <button type="submit" class="button-second montserrat-semibold submit-btn">
            –ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç—å –∑–∞–∫–∞–∑
          </button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import {products} from '@/data/products'
import { boxes, calculateBoxPrice } from '@/data/boxes'
import sendMessage from '@/utils/telegramUtils'


// ============= –£–¢–ò–õ–ò–¢–´ =============
// –ï—Å–ª–∏ —É —Ç–µ–±—è –Ω–µ—Ç –æ—Ç–¥–µ–ª—å–Ω—ã—Ö —Ñ–∞–π–ª–æ–≤ —É—Ç–∏–ª–∏—Ç, –º–æ–∂–µ—à—å –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å —ç—Ç–∏ —Ñ—É–Ω–∫—Ü–∏–∏
// –ò–ª–∏ —Å–æ–∑–¥–∞–π —Ñ–∞–π–ª—ã /utils/cartUtils.js –∏ /utils/favoritesUtils.js

const CART_KEY = 'box_cart'
const FAVORITES_KEY = 'box_favorites'
const token = "7233293424:AAGeCDzVlrxqi8PNyjcTz8bpfseUSsW08NA"; // –Ω–∞–ø—Ä–∏–º–µ—Ä
const chatId = -1002950935614;

const cartUtils = {
  getCart() {
    try {
      const cart = localStorage.getItem(CART_KEY)
      return cart ? JSON.parse(cart) : { items: [] }
    } catch (e) {
      console.error('Error reading cart:', e)
      return { items: [] }
    }
  },

  saveCart(cart) {
    try {
      localStorage.setItem(CART_KEY, JSON.stringify(cart))
    } catch (e) {
      console.error('Error saving cart:', e)
    }
  },

  addBox(boxId) {
    const cart = this.getCart()
    const box = boxes.find(b => b.id === boxId)
    if (!box) return null
    
    const cartItem = {
      id: `cart-${Date.now()}-${Math.random()}`,
      boxId,
      customComposition: null,
      totalPrice: calculateBoxPrice(boxId),
      addedAt: Date.now()
    }
    cart.items.push(cartItem)
    this.saveCart(cart)
    return cartItem
  },

  updateBoxComposition(cartItemId, customComposition) {
    const cart = this.getCart()
    const item = cart.items.find(i => i.id === cartItemId)
    if (item) {
      item.customComposition = customComposition
      item.totalPrice = customComposition.reduce((sum, comp) => {
        const product = products[comp.productId]
        return sum + (product.price * comp.quantity)
      }, 0)
      this.saveCart(cart)
    }
  },

  removeItem(cartItemId) {
    const cart = this.getCart()
    cart.items = cart.items.filter(i => i.id !== cartItemId)
    this.saveCart(cart)
  },

  clear() {
    this.saveCart({ items: [] })
  }
}

const favoritesUtils = {
  getFavorites() {
    try {
      const favs = localStorage.getItem(FAVORITES_KEY)
      return favs ? JSON.parse(favs) : { boxIds: [] }
    } catch (e) {
      console.error('Error reading favorites:', e)
      return { boxIds: [] }
    }
  },

  saveFavorites(favorites) {
    try {
      localStorage.setItem(FAVORITES_KEY, JSON.stringify(favorites))
    } catch (e) {
      console.error('Error saving favorites:', e)
    }
  },

  isFavorite(boxId) {
    const favs = this.getFavorites()
    return favs.boxIds.includes(boxId)
  },

  toggle(boxId) {
    const favs = this.getFavorites()
    const index = favs.boxIds.indexOf(boxId)
    
    if (index > -1) {
      favs.boxIds.splice(index, 1)
    } else {
      favs.boxIds.push(boxId)
    }
    
    this.saveFavorites(favs)
    return index === -1
  },

  remove(boxId) {
    const favs = this.getFavorites()
    favs.boxIds = favs.boxIds.filter(id => id !== boxId)
    this.saveFavorites(favs)
  }
}

export default {
  name: 'CartPage',
  data() {
    return {
      activeTab: 'cart',
      promoCode: '',
      discount: 0,
      deliveryCost: 0,
      showCheckoutModal: false,
      orderForm: {
        name: '',
        phone: '',
        telegram: '',
        address: '',
        comment: ''
      },
      cart: { items: [] },
      favoriteBoxIds: []
    }
  },
  computed: {
    // –ü–æ–¥–≥–æ—Ç–æ–≤–ª–µ–Ω–Ω—ã–µ —ç–ª–µ–º–µ–Ω—Ç—ã –∫–æ—Ä–∑–∏–Ω—ã
    cartItems() {
      return this.cart.items.map(item => {
        const box = boxes.find(b => b.id === item.boxId)
        if (!box) return null

        const composition = item.customComposition || box.defaultComposition
        
        const displayComposition = composition.map(comp => ({
          ...comp,
          name: products[comp.productId].name
        }))

        return {
          ...item,
          box,
          displayComposition,
          isModified: !!item.customComposition
        }
      }).filter(Boolean)
    },

    // –ò–∑–±—Ä–∞–Ω–Ω—ã–µ –±–æ–∫—Å—ã
    favoriteBoxes() {
      return this.favoriteBoxIds
        .map(id => boxes.find(b => b.id === id))
        .filter(Boolean)
    },

    totalItems() {
      return this.cartItems.length
    },

    subtotal() {
      return this.cartItems.reduce((sum, item) => sum + item.totalPrice, 0)
    },

    total() {
      let deliveryCost = this.deliveryCost
      if (this.subtotal >= 5000) {
        deliveryCost = 0
      }
      return this.subtotal - this.discount + deliveryCost
    }
  },
  methods: {
    // –ó–∞–≥—Ä—É–∑–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É
    loadCart() {
      this.cart = cartUtils.getCart()
    },

    OrderCart() {
      (async () => {
        try {
          const responses = await sendMessage(token, chatId, JSON.stringify(this.cart), {
            parse_mode: 'HTML', // –∏–ª–∏ 'MarkdownV2' –∏–ª–∏ omit
            disable_web_page_preview: true
          });
          console.log('–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ —á–∞—Å—Ç–µ–π:', responses.length);
        } catch (err) {
          console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ —Å–æ–æ–±—â–µ–Ω–∏—è:', err);
        }
      })();
    },

    // –ó–∞–≥—Ä—É–∑–∏—Ç—å –∏–∑–±—Ä–∞–Ω–Ω–æ–µ
    loadFavorites() {
      const favs = favoritesUtils.getFavorites()
      this.favoriteBoxIds = favs.boxIds
    },

    // –£–¥–∞–ª–∏—Ç—å –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
    removeFromCart(cartItemId) {
        cartUtils.removeItem(cartItemId)
        this.loadCart()
    },

    // –£–¥–∞–ª–∏—Ç—å –∏–∑ –∏–∑–±—Ä–∞–Ω–Ω–æ–≥–æ
    removeFromFavorites(boxId) {
      favoritesUtils.remove(boxId)
      this.loadFavorites()
    },

    // –î–æ–±–∞–≤–∏—Ç—å –±–æ–∫—Å –≤ –∫–æ—Ä–∑–∏–Ω—É (–∏–∑ –∏–∑–±—Ä–∞–Ω–Ω–æ–≥–æ)
    addBoxToCart(boxId) {
      cartUtils.addBox(boxId)
      this.loadCart()
      this.activeTab = 'cart'
    },

    // –ü–µ—Ä–µ–π—Ç–∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É –±–æ–∫—Å–∞
    goToBoxPage(slug) {
      // –ò—Å–ø–æ–ª—å–∑—É–π —Å–≤–æ–π —Ä–æ—É—Ç–µ—Ä
      console.log('Navigate to:', `/box/${slug}`)
      // this.$router.push(`/box/${slug}`)
    },

    // –ü—Ä–∏–º–µ–Ω–∏—Ç—å –ø—Ä–æ–º–æ–∫–æ–¥
    applyPromo() {
      if (this.promoCode.toLowerCase() === 'sale10') {
        this.discount = Math.floor(this.subtotal * 0.1)
        alert('–ü—Ä–æ–º–æ–∫–æ–¥ –ø—Ä–∏–º–µ–Ω—ë–Ω! –°–∫–∏–¥–∫–∞ 10%')
      } else if (this.promoCode) {
        alert('–ü—Ä–æ–º–æ–∫–æ–¥ –Ω–µ –Ω–∞–π–¥–µ–Ω')
      }
    },

    // –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    openCheckoutModal() {
      this.showCheckoutModal = true
    },

    closeCheckoutModal() {
      this.showCheckoutModal = false
    },

    // –û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑
    submitOrder() {
      console.log('Order submitted:', {
        items: this.cart.items,
        total: this.total,
        customer: this.orderForm
      })
      
      alert(`–ó–∞–∫–∞–∑ –æ—Ñ–æ—Ä–º–ª–µ–Ω! –ú—ã —Å–≤—è–∂–µ–º—Å—è —Å –≤–∞–º–∏ –ø–æ —Ç–µ–ª–µ—Ñ–æ–Ω—É ${this.orderForm.phone} –∏–ª–∏ –≤ Telegram ${this.orderForm.telegram}`)
      
      this.orderForm = {
        name: '',
        phone: '',
        telegram: '',
        address: '',
        comment: ''
      }
      cartUtils.clear()
      this.loadCart()
      this.closeCheckoutModal()
    },

    // –í—Å–ø–æ–º–æ–≥–∞—Ç–µ–ª—å–Ω—ã–µ –º–µ—Ç–æ–¥—ã
    formatPrice(price) {
      return price.toLocaleString('ru-RU')
    },

    calculateBoxPrice(boxId) {
      return calculateBoxPrice(boxId)
    },

    getShortCompositionNames(boxId) {
      const box = boxes.find(b => b.id === boxId)
      if (!box) return []
      
      return box.defaultComposition
        .slice(0, 5)
        .map(item => products[item.productId].name)
    }
  },
  mounted() {
    this.loadCart()
    this.loadFavorites()
  }
}
</script>

<style scoped>
:root {
  --color-full-white: #FFFFFF;
  --color-full-black: #000000;
  --color-light-gray: #F5F5F5;
  --color-gray: #D9D9D9;
  --color-dark-second: #198F52;
  --color-medium-second: #35C66B;
}

.cart-page {
  min-height: 100vh;
  padding: 60px 80px;
  max-width: 1400px;
  margin: 0 auto;
}

/* Tabs */
.tabs {
  display: flex;
  gap: 30px;
  margin-bottom: 50px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.1);
}

.tab {
  background: none;
  border: none;
  color: var(--color-gray);
  padding: 15px 0;
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;
}

.tab:hover {
  color: var(--color-light-gray);
}

.tab.active {
  color: var(--color-full-white);
}

.tab.active::after {
  content: '';
  position: absolute;
